@mixin title {
    font-size: 25px;
    font-family: $font-title;
    text-align: left;
    color: $color-primary-dark;
    text-transform: uppercase;

    margin-bottom: 70px;

    &::after {
        display: block;
        width: 100%;
        height: 2px;
        margin: 20px auto 70px;

        content: '';

        background-color: $color-tertiary; } }
